@use './variables' as *;

/* icon fonts */
@font-face {
  font-family: 'posse-icon';
  src:
    url('../fonts/posse-icon.ttf?bbqgr3') format('truetype'),
    url('../fonts/posse-icon.woff?bbqgr3') format('woff'),
    url('../fonts/posse-icon.svg?bbqgr3#posse-icon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: inline-block;
}

i, .icomoon-liga {
  font-family: 'posse-icon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  letter-spacing: 0;
  -webkit-font-feature-settings: "liga";
  -moz-font-feature-settings: "liga=1";
  -moz-font-feature-settings: "liga";
  -ms-font-feature-settings: "liga" 1;
  font-feature-settings: "liga";
  -webkit-font-variant-ligatures: discretionary-ligatures;
  font-variant-ligatures: discretionary-ligatures;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.u-icon__link:before {
  content: "\e901";
}
.u-icon__note:before {
  content: "\e902";
}
.u-icon__arrow:before {
  content: "\e903";
}
.u-icon__line:before {
  content: "\e900";
  color: #00c300;
  background: radial-gradient(#fff 60%, transparent 60%);
}
.u-icon__instagram:before {
  content: "\ea92";
}
.u-icon__twitter:before {
  content: "\ea96";
}

/* body */
body {
  font-family: $font-default;
}
